.patientInfo {
  width: 1120px;
  height: 800px;
  display: flex;
  flex-direction: column;
  line-height: normal;
  margin: 0 auto;
}

/* ---------- patientInfo-header ---------- */
.patientInfo-header {
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: Inter; /* 공통부분 */
  margin: 10px 0 20px 0;
}

.patientInfo-header-title {
  font-size: 38px;
  font-weight: 700;
}
.patientInfo-header-back {
  display: flex;
  align-items: center;
  font-size: 32px;
  font-weight: 700;
  color: black;
}
.patientInfo-header-back::before {
  content: "";
  display: block;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: #a9d2e1;
  background-image: url("../images/back.png");
  background-position: center;
  background-repeat: no-repeat;
  margin-right: 5px;
}
.patientInfo-header-link {
  text-decoration: none;
}
.patientInfo-header-complete {
  display: flex;
  align-items: center;
  font-size: 32px;
  font-weight: 700;
  color: black;
}
.patientInfo-header-complete::before {
  content: "";
  display: block;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: #C0DEBB;
  background-image: url("../images/complete.png");
  background-position: center;
  background-repeat: no-repeat;
  margin-right: 5px;
}

.patientInfo-header-user {
  width: 260px;
  height: 70px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  background-color: aqua;
  border-radius: 15px;
  box-shadow: 3px 4px 4px 0px rgba(0, 0, 0, 0.25);
}
.patientInfo-header-user-name {
  font-size: 20px;
  font-weight: 400;
  margin-right: 10px;
}
.patientInfo-header-user-icon {
  width: 60px;
  height: 60px;
  background-color: red;
  border-radius: 15px;
  border: 1px solid black;
}

/* ---------- patientInfo-content ---------- */
.patientInfo-content {
  width: 1060px; /* padding뺀 값 */
  height: 900px;
  display: flex;
  flex-direction: column;
  position: relative;
  font-family: Inter; /* 공통부분 */
  background-color: #d9d9d9;
  padding: 30px;
}

.patientInfo-content-header {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}
.patientInfo-content-header-name {
  width: 300px;
  font-size: 28px;
  font-weight: 700;
  color: #000;
  background: none;
  border: none;
  margin-right: 70px;
}
.patientInfo-content-header-name:focus {
  outline: none;
}
.patientInfo-content-header-chat {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.patientInfo-content-header-chat-icon {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: #F4B4B4;
  margin-right: 5px;
}
.patientInfo-content-header-chat-icon::before {
  content: "";
  display: block;
  width: 25px;
  height: 25px;
  background-image: url("../images/live\ chat.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}
.patientInfo-content-header-chat-title {
  font-size: 28px;
  font-weight: 700;
  color: #000;
}

.patientInfo-content-section {
  width: 100%;
  display: flex;
}
.patientInfo-content-section-left {
  width: 685px;
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
}

/* 모두다 껍데기 보다 입력칸이 30px적게 설정 */
.patientInfo-content-id {
  width: 370px;
  height: 330px;
  display: flex;
  flex-direction: column;
}
.patientInfo-content-id-title {
  font-size: 22px;
  font-weight: 700;
  color: black;
}
.patientInfo-content-id-box {
  width: 100%;
  height: 300px;
  background-color: white;
  border: 1px solid black;
  overflow: hidden;
}
.patientInfo-content-id-box-item {
  width: 100%;
  display: flex;
  border-bottom: 1px solid black;
  line-height: 1.5;
}
.patientInfo-content-id-box-item-title {
  width: 140px;
  color: #000;
  text-align: center;
  font-family: Inter;
  font-size: 18px;
  font-weight: 700;
  border-right: 1px solid black;
}
.patientInfo-content-id-box-item-title-last {
  height: 80px;
  width: 140px;
  color: #000;
  text-align: center;
  font-family: Inter;
  font-size: 18px;
  font-weight: 700;
  border-right: 1px solid black;
  line-height: 80px;
}
.patientInfo-content-id-box-item-text {
  width: calc(100% - 140px);
  color: #000;
  text-align: center;
  font-family: Inter;
  font-size: 18px;
  font-weight: 400;
  border: none;
  padding-left: 5px;
}
.patientInfo-content-id-box-item-text:focus {
  outline: none;
}

.patientInfo-content-memo {
  width: 300px;
  height: 330px;
  display: flex;
  flex-direction: column;
  margin-left: 15px;
}
.patientInfo-content-memo-title {
  font-size: 22px;
  font-weight: 700;
  color: black;
}
.patientInfo-content-memo-box {
  min-width: 290px;
  min-height: 280px;
  max-width: 290px;
  max-height: 280px;
  background-color: #FFF3DB;
  border: 1px solid black;
  padding: 10px;
  line-height: 1.5;
}

.patientInfo-content-file {
  width: 685px;
  height: 360px;
  display: flex;
  flex-direction: column;
  margin-top: 15px;
}
.patientInfo-content-file-title {
  font-size: 22px;
  font-weight: 700;
  color: black;
}
.patientInfo-content-file-box {
  width: 100%;
  height: 330px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  background-color: white;
  border: 1px solid black;
  overflow-y: scroll;
  padding: 6px;
}
.patientInfo-content-file-box-submit {
  width: 24px;
  height: 24px;
  background-color: #F6F6F6;
  background-image: url("../images/upload.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  border: 1px solid black;
  border-radius: 8px;
  margin: 6px;
}
.patientInfo-content-file-box-item {
  display: none;
}
.patientInfo-content-file-box-file {
  width: 96%;
  height: 26px;
  border: 2px solid #87B1BF;
  border-radius: 6px;
  line-height: 1.5;
  margin: 6px;
  padding-left: 10px;
  cursor: pointer;
}
.patientInfo-content-file-box::-webkit-scrollbar {
  width: 8px; /* 스크롤 바의 너비 */
  height: 8px; /* 스크롤 바의 높이 */
  background-color: none; /* 스크롤 바의 배경색 */
}
.patientInfo-content-file-box::-webkit-scrollbar-thumb {
  background: none;
  border-radius: 4px;
}
.patientInfo-content-file-box:hover::-webkit-scrollbar-thumb {
  overflow: visible;
  background: #c2c2c2;
}
.patientInfo-content-section-right {
  width: 355px;
  height: 700px;
  position: relative;
  margin-top: 26.5px;
  margin-left: 20px;
}
.patientInfo-content-chat {
  position: absolute;
  font-size: 22px;
  font-weight: 700;
  color: black;
}
.patientInfo-content-chat-box {
  width: 100%;
  height: 100%;
  background-color: #FCA7A7;
  border: 1px solid black;
}